.serviceHeading {
  background: linear-gradient(to right, #0dc5db 0%, #00a8b8 60%);
}
@media screen and (min-width: 768px) {
  .serviceHeading {
    background-image: url('/static/images/service_header_bg.png');
    background-position: center;
    background-repeat: no-repeat;
    margin-bottom: 0;
  }
}
@media screen and (min-width: 992px) {
  .serviceHeading {
    background-position: right;
  }
}
.serviceHeadingInner {
  align-items: center;
  display: flex;
  padding: 15px;
}
@media screen and (min-width: 768px) {
  .serviceHeadingInner {
    padding: 15px 15px 67px 15px;
  }
}
@media screen and (min-width: 992px) {
  .serviceHeadingInner {
    padding-bottom: 89px;
    padding-left: 104px;
    padding-top: 58px;
  }
}
.serviceHeadingInner img {
  margin-right: 38px;
}
.serviceHeadingInner span {
  color: #FFFFFF;
  font-size: 20px;
  text-transform: uppercase;
}
@media screen and (min-width: 768px) {
  .serviceHeadingInner span {
    font-size: 28px;
  }
}
@media screen and (min-width: 992px) {
  .serviceHeadingInner span {
    font-size: 38px;
  }
}
@media screen and (min-width: 768px) {
  .serviceSlider {
    width: 50%;
    display: flex;
    justify-content: center;
  }
}
.serviceContent {
  display: flex;
  flex-direction: column;
  margin: 20px 0;
}
@media screen and (min-width: 768px) {
  .serviceContent {
    flex-direction: row;
  }
}
.serviceContentText {
  color: #777777;
  text-align: center;
  padding-top: 10px;
}
@media screen and (min-width: 768px) {
  .serviceContentText {
    padding: 0;
    text-align: initial;
    width: 50%;
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
  }
}
.serviceContentText h2 {
  color: #ff415c;
  font-family: 'Circe', Arial, sans-serif;
  font-size: 22px;
  text-transform: uppercase;
}
@media screen and (min-width: 992px) {
  .serviceContentText h2 {
    font-size: 38px;
  }
}
.serviceContentText p {
  padding-bottom: 20px;
}
.serviceContentSlider {
  display: flex;
  justify-content: center;
}
@media screen and (min-width: 768px) {
  .serviceContentSlider {
    width: 50%;
  }
}
.carousel {
  box-shadow: 0 0px 6px rgba(0, 0, 0, 0.16), 0 0px 6px rgba(0, 0, 0, 0.23);
  position: relative;
  height: 290px;
  width: 290px;
  overflow: hidden;
  margin-top: 20px;
}
@media screen and (min-width: 992px) {
  .carousel {
    width: 500px;
    min-width: 500px;
    height: 395px;
  }
}
@media screen and (min-width: 1200px) {
  .carousel {
    width: 623px;
    min-width: 623px;
    height: 492px;
  }
}
.slide {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 60%;
  display: inline-block;
  width: 290px;
  height: 100%;
}
@media screen and (min-width: 992px) {
  .slide {
    width: 500px;
    min-width: 500px;
    height: 395px;
  }
}
@media screen and (min-width: 1200px) {
  .slide {
    width: 623px;
    min-width: 623px;
    height: 492px;
  }
}
