.orderHeading {
  background: linear-gradient(to right, #00baab 0%, #00c0cd 53%, #00c3da 100%);
}
@media screen and (min-width: 768px) {
  .orderHeading {
    background-image: url('/static/images/order_header_bg.png');
    background-position: top center;
    background-size: 150%;
    background-repeat: no-repeat;
    margin-bottom: 0;
  }
}
@media screen and (min-width: 992px) {
  .orderHeading {
    background-position: right;
    background-size: initial;
  }
}
.orderHeadingInner {
  align-items: center;
  display: flex;
  padding: 15px;
}
@media screen and (min-width: 768px) {
  .orderHeadingInner {
    padding: 15px 15px 67px 15px;
  }
}
@media screen and (min-width: 992px) {
  .orderHeadingInner {
    padding-bottom: 89px;
    padding-left: 104px;
    padding-top: 58px;
  }
}
.orderHeadingInner img {
  margin-right: 38px;
}
.orderHeadingInner span {
  color: #FFFFFF;
  font-size: 20px;
  text-transform: uppercase;
}
@media screen and (min-width: 768px) {
  .orderHeadingInner span {
    font-size: 28px;
  }
}
@media screen and (min-width: 992px) {
  .orderHeadingInner span {
    font-size: 38px;
  }
}
.orderContent {
  display: flex;
  flex-direction: column;
  margin: 20px 0;
}
@media screen and (min-width: 768px) {
  .orderContent {
    flex-direction: row;
  }
}
.orderContentSlider {
  display: flex;
  justify-content: center;
}
@media screen and (min-width: 768px) {
  .orderContentSlider {
    width: 50%;
  }
}
.orderContentText {
  color: #777777;
  text-align: center;
  padding-top: 10px;
}
@media screen and (min-width: 768px) {
  .orderContentText {
    padding: 0;
    text-align: initial;
    width: 50%;
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
  }
}
.orderContentText h2 {
  color: #ff415c;
  font-family: 'Circe', Arial, sans-serif;
  font-size: 22px;
  text-transform: uppercase;
}
@media screen and (min-width: 992px) {
  .orderContentText h2 {
    font-size: 38px;
  }
}
.orderContentText p {
  padding-bottom: 20px;
}
.carousel {
  box-shadow: 0 0px 6px rgba(0, 0, 0, 0.16), 0 0px 6px rgba(0, 0, 0, 0.23);
  position: relative;
  height: 290px;
  width: 290px;
  min-width: 290px;
  overflow: hidden;
}
@media screen and (min-width: 992px) {
  .carousel {
    width: 500px;
    min-width: 500px;
    height: 395px;
  }
}
@media screen and (min-width: 1200px) {
  .carousel {
    width: 623px;
    min-width: 623px;
    height: 492px;
  }
}
.slide {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 60%;
  display: inline-block;
  width: 290px;
  height: 100%;
}
@media screen and (min-width: 992px) {
  .slide {
    width: 500px;
    min-width: 500px;
    height: 395px;
  }
}
@media screen and (min-width: 1200px) {
  .slide {
    width: 623px;
    min-width: 623px;
    height: 492px;
  }
}
