.button {
  background: #ff415c;
  border-radius: 10px;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  display: inline-block;
  font-size: 18px;
  line-height: 1;
  padding: 14px 30px;
  text-transform: uppercase;
  outline: none;
}
.button:hover,
.button:focus {
  background: #ff5b72;
}
@media screen and (min-width: 992px) {
  .button {
    font-size: 18px;
    padding: 28px 70px;
  }
}
