.posts--container {
  max-width: 678px;
  padding: 0 15px;
  margin: 0 auto;
}
.post-item--card {
  border-width: 1px;
  border-radius: 5px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.1);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 4px;
  margin-bottom: 30px;
  padding: 15px;
}
.post-item--card img {
  cursor: pointer;
  max-width: 100%;
  height: auto;
}
.post-item--card h2 {
  cursor: pointer;
  font-size: 30px;
  margin-bottom: 0;
  text-transform: uppercase;
  font-family: Circe;
  font-weight: 200;
}
.post-item--card h5 {
  cursor: pointer;
  color: #9c9c9c;
  font-size: 18px;
  margin-top: 0;
  text-transform: uppercase;
  font-family: Circe;
  line-height: 1;
}
.post-item--card--excerpt {
  cursor: pointer;
  font-size: 14px;
}
.posts--details-cover {
  position: relative;
}
.posts--details-cover--caption {
  position: absolute;
  bottom: 15px;
  right: 15px;
  opacity: 0.4;
}
.posts--details-cover--caption:hover {
  opacity: 1;
}
.posts--details-header {
  font-family: 'Circe', Arial, sans-serif;
  text-transform: uppercase;
}
.posts--details-header h1 {
  color: #ff415c;
  font-weight: bold;
  margin-bottom: 0;
}
.posts--details-header h2 {
  color: #b9b9b9;
  margin-top: 0;
}
.posts--content img {
  max-width: 100%;
  height: auto;
  margin: 20px 0;
}
