.promo {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 992px) {
  .promo {
    flex-direction: row;
  }
}
.promoCarousel {
  min-width: 50%;
}
.promoCarousel img {
  width: 100%;
}
.promoText {
  background-color: #a9edf5;
  position: relative;
}
@media screen and (min-width: 992px) {
  .promoText {
    background-image: none;
    width: 50%;
  }
}
.promoTextInner {
  background-image: url("/static/images/hzbg-big.png");
  background-position: 23% 100%;
  box-sizing: border-box;
  color: #FFFFFF;
  min-width: 50%;
  padding: 13.61639824%;
  height: 100%;
}
@media screen and (min-width: 992px) {
  .promoTextInner {
    background-image: url(/static/images/hzbg-big.png);
    background-position: left;
    box-sizing: border-box;
    color: #FFFFFF;
    min-width: 50%;
    padding: 13.61639824%;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    width: 120%;
    height: 100%;
    top: 0;
    left: -20%;
    padding: 36% 15% 8% 38%;
  }
}
@media screen and (min-width: 1200px) {
  .promoTextInner {
    padding: 41% 44% 10% 26%;
  }
}
.promoTextInner h1 {
  font-size: 26px;
  font-weight: 300;
  text-transform: uppercase;
}
.promoTextInner h3 {
  font-weight: 200;
  text-transform: uppercase;
}
