.catalogHeading {
  background: linear-gradient(to right, #0dc5db 0%, #00a8b8 60%);
  background-position: center;
  background-repeat: no-repeat;
  margin-bottom: 20px;
}
@media screen and (min-width: 768px) {
  .catalogHeading {
    background-image: url('/static/images/catalog_header_bg.png');
    margin-bottom: 0;
  }
}
.catalogHeadingInner {
  align-items: center;
  display: flex;
  padding: 15px;
}
@media screen and (min-width: 768px) {
  .catalogHeadingInner {
    padding: 15px 15px 67px 15px;
  }
}
@media screen and (min-width: 992px) {
  .catalogHeadingInner {
    padding-bottom: 89px;
    padding-left: 104px;
    padding-top: 58px;
  }
}
.catalogHeadingInner img {
  margin-right: 38px;
}
.catalogHeadingInner span {
  color: #FFFFFF;
  font-size: 20px;
  text-transform: uppercase;
}
@media screen and (min-width: 768px) {
  .catalogHeadingInner span {
    font-size: 28px;
  }
}
@media screen and (min-width: 992px) {
  .catalogHeadingInner span {
    font-size: 38px;
  }
}
.catalogList {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .catalogList {
    align-items: initial;
    flex-direction: row;
  }
}
.catalogItem {
  cursor: pointer;
  padding: 5px;
  width: 290px;
}
@media screen and (min-width: 768px) {
  .catalogItem {
    width: 30%;
  }
}
.catalogItem .imgContainer {
  align-items: center;
  border: 2px solid transparent;
  box-shadow: 0 0px 6px rgba(0, 0, 0, 0.16), 0 0px 6px rgba(0, 0, 0, 0.23);
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  padding: 10%;
  height: 290px;
  width: 100%;
}
@media screen {
  .catalogItem .imgContainer {
    height: 330px;
  }
}
@media screen and (min-width: 992px) {
  .catalogItem .imgContainer {
    height: 428px;
  }
}
.catalogItem .imgContainer img {
  max-height: 100%;
  max-width: 100%;
}
.catalogItemName {
  color: #4BAEC2;
  font-size: 18px;
  padding: 39px 0;
  position: relative;
  text-align: center;
  text-transform: uppercase;
}
@media screen and (min-width: 768px) {
  .catalogItemName {
    font-size: 27px;
  }
}
@media screen and (min-width: 768px) {
  .catalogItem:not(:last-of-type) .catalogItemName:after {
    border-right: 2px solid #EEEFF0;
    content: '';
    height: 105px;
    position: absolute;
    right: -6px;
    top: 5px;
    width: 2px;
  }
}
.catalogItem:hover .imgContainer {
  border: 2px solid #ff415c;
}
.catalogItem:hover .catalogItemName {
  color: #ff415c;
}
